<template>
	<view style="padding: 20rpx;">
		<view style="padding: 30rpx;background-color: rgba(255, 255, 255, 1);">
			<view style="font-size: 30rpx;font-weight: 600;margin: 10rpx 0;">基础信息</view>
			<view class="infoBox">
				<view class="infoItem">
					<view style="float: left;">处置时间：</view>
					<view style="float: right;">{{info.handleDate}}</view>
				</view>
				<view class="infoItem">
					<view style="float: left;">交接负责人：</view>
					<view style="float: right;">{{info.newHandlerName}}</view>
				</view>
				<view class="infoItem">
					<view style="float: left;">接收单位：</view>
					<view style="float: right;">{{info.fullName}}</view>
				</view>
				<view class="infoItem">
					<view style="float: left;">接收人员：</view>
					<view style="float: right;">{{info.receiver}}</view>
				</view>
				<view class="infoItem">
					<view style="float: left;">接收人身份证：</view>
					<view style="float: right;">{{info.receiverIdentityCard}}</view>
				</view>
				<view style="float: left;width: 100%;line-height: 52rpx;padding: 10rpx 0;border-bottom: 1rpx solid rgba(234, 234, 234, 1);">
					<view style="">废弃物处理登记：</view>
					<view style="margin-left: 30rpx;overflow: hidden;">
						<view>
							<view style="width: 140rpx;text-align: right;float: left;">泔水数量</view>
							<view style="width: 200rpx;text-align: center;float: left;height: 50rpx;border: 2rpx solid rgba(234, 234, 234, 1);line-height: 50rpx;margin: 0 16rpx;">{{info.swillNumber}}</view>公斤
						</view>
						<view>
							<view style="width: 140rpx;text-align: right;float: left;">废油数量</view>
							<view style="width: 200rpx;text-align: center;float: left;height: 50rpx;border: 2rpx solid rgba(234, 234, 234, 1);line-height: 50rpx;margin: 0 16rpx;">{{info.wasteoilNumber}}</view>公斤
						</view>
						<view>
							<view style="width: 140rpx;text-align: right;float: left;">其它废弃物</view>
							<view style="width: 200rpx;text-align: center;float: left;height: 50rpx;border: 2rpx solid rgba(234, 234, 234, 1);line-height: 50rpx;margin: 0 16rpx;">{{info.otherWasteNumber}}</view>公斤
						</view>
					</view>
					<!-- <view style="float: right;">{{info.foodName}}</view> -->
				</view>
				<view style="float: left;width: 100%;line-height: 52rpx;padding: 10rpx 0;" >
					<view style="float: left;margin-bottom: 16rpx;">备注：</view>
					<view style="float: left;background-color: rgba(234, 234, 234, 1);width: 100%;border-radius: 10rpx;min-height: 200rpx;word-break: break-all;padding: 10rpx;">
						{{info.note}}
					</view>
				</view>
				<view style="float: left;width: 100%;line-height: 52rpx;padding: 16rpx 0;">拍摄处理场景图</view>
				<image :src="item" v-for="(item,index) in imgs" style="border-radius: 10rpx;"></image>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		LedgerInfo
	} from '@/api/canteenManage/money.js';
	export default {
		data() {
			return {
				id: -1,
				info:{},
				imgs:[],
			}
		},
		onLoad(opt) {
			this.id = opt.id;
			this.getSampleInfo();
		},
		methods: {
			getSampleInfo() {
				LedgerInfo(this.id).then(res => {
					this.info=res.data.data;
					this.imgs=res.data.data.imgs.split(',');
					for(let i=0;i<this.imgs.length;i++){
						if(i>0){
							this.imgs[i]=this.imgs[i];
						}
					}
				});
			}
		}
	}
</script>

<style>
.infoItem{
	float: left;
	width: 100%;
	line-height: 52rpx;
	padding: 16rpx 0;
	border-bottom: 1rpx solid rgba(234, 234, 234, 1);
}
</style>
